import React, { Component } from 'react';
import ModalImage from 'react-modal-image'
import ReactEmbedGist from 'react-embed-gist';

function ProjectModalImage(props) {
  return ( 
    <ModalImage 
      small={require(""+props.imgPath)} 
      large={require(""+props.imgPath)}
      hideDownload="true" 
      hideZoom="true"/>
  );
}

class Project extends Component {
  constructor(props) {
    super(props); 
  }
  render() {
    switch(this.props.link) {
      case "test": 
        return (
          <div className="Project">
            <h>yeep</h>
            Hi
          </div>
        );
      case "home": 
        return (
          <div className="Project"><br></br> 
            <div id="frontpage">
              <ProjectModalImage imgPath="../assets/thumbnails/test.jpg" />
              <ModalImage className="smallImg" small={require("../assets/thumbnails/test.jpg")} hideDownload="true" hideZoom="true"/>
            </div>
          </div>
        );
    }
    return (
      <div className="Project">
        this page doesn't exist. yikes! 
      </div>
    );
  }
}

export default Project;